// 查询列表接口
export const getCategoryPage = (params: any) => {
  return request({
    url: '/category/page',
    method: 'get',
    params
  })
}

// 编辑页面反查详情接口
export const queryCategoryById = (id: string | (string | null)[]) => {
  return request({
    url: `/category/${id}`,
    method: 'get'
  })
}

// 删除当前列的接口
export const deleCategory = (ids: string) => {
  return request({
    url: '/category',
    method: 'delete',
    params: { ids }
  })
}

// 修改接口
export const editCategory = (params: any) => {
  return request({
    url: '/category',
    method: 'put',
    data: { ...params }
  })
}

// 新增接口
export const addCategory = (params: any) => {
  return request({
    url: '/category',
    method: 'post',
    data: { ...params }
  })
}
