













































































































import { Component, Vue } from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
import AddDish from './components/AddDish.vue'
import { querySetmealById, addSetmeal, editSetmeal } from '@/api/setMeal'
import { getCategoryList } from '@/api/dish'
import { baseUrl } from '@/config.json'

@Component({
  'name': 'addShop',
  'components': {
    HeadLable,
    AddDish
  }
})
export default class extends Vue {
  private value: string = ''
  private setMealList: [] = []
  private seachKey: string = ''
  private dishList: [] = []
  private imageUrl: string = ''
  private actionType: string = ''
  private dishTable: [] = []
  private dialogVisible: boolean = false
  private checkList: any[] = []
  private ruleForm = {
    'name': '',
    'categoryId': '',
    'price': '',
    'code': '',
    'image': '',
    'description': '',
    'dishList': [],
    'status': true,
    'idType': ''
  }

  get rules() {
    return {
      'name': {
        'required': true,
        'message': '请输入套餐名称',
        'trigger': 'blur'
      },
      'idType': {
        'required': true,
        'message': '请选择套餐分类',
        'trigger': 'change'
      },
      'price': {
        'required': true,
        // 'message': '请输入套餐价格',
        validator: (rules: any, value: string, callback: Function) => {
          if (!value) {
            callback(new Error('请填写套餐价格'))
          } else {
            const reg = /^((0{1}\.\d{1,2})|([1-9]\d*\.{1}\d{1,2})|([1-9]+\d*)|0)$/
            if (reg.test(String(value))) {
              // callback()
              if (String(value).includes('.')) {
                let temp = Number(String(value).split('.')[0])
                if (temp > 9999 || temp < 0) {
                  callback(new Error('套餐价格区间在0-9999之间'))
                } else {
                  callback()
                }
              } else {
                if (Number(value) > 9999 || Number(value) < 0) {
                  callback(new Error('套餐价格区间在0-9999之间'))
                } else {
                  callback()
                }
              }
            } else {
              callback(new Error('套餐价格格式只能为正整数'))
            }
          }
        },
        'trigger': 'blur'
      },
      'code': { 'required': true, 'message': '请输入商品码', 'trigger': 'blur' }
    }
  }

  created() {
    this.getDishTypeList()
    this.actionType = this.$route.query.id ? 'edit' : 'add';
    if (this.actionType == 'edit') {
      this.init()
    }
  }

  private async init() {
    querySetmealById(this.$route.query.id).then(res => {
      if (res && res.data && res.data.code === 1) {
        this.ruleForm = res.data.data
        this.ruleForm.status = res.data.data.status === '1';
        (this.ruleForm as any).price = res.data.data.price / 100
        // this.imageUrl = `http://172.17.2.120:8080/common/download?name=${res.data.data.image}`
        this.imageUrl = `${baseUrl}/common/download?name=${res.data.data.image}`
        this.checkList = res.data.data.setmealDishes
        this.dishTable = res.data.data.setmealDishes
        this.ruleForm.idType = res.data.data.categoryId
      } else {
        this.$message.error(res.data.msg)
      }
    })
  }
  private seachHandle() {
    this.seachKey = this.value
  }
  // 获取套餐分类
  private getDishTypeList() {
    getCategoryList({ 'type': 2, 'page': 1, 'pageSize': 1000 }).then(res => {
      if (res && res.data && res.data.code === 1) {
        this.setMealList = res.data.data.map((obj: any) => ({ ...obj, idType: obj.id }))
      } else {
        this.$message.error(res.data.msg)
      }
    })
  }

  // 通过套餐ID获取菜品列表分类
  // private getDishList (id:number) {
  //   getDishListType({id}).then(res => {
  //     if (res.data.code == 200) {
  //       const { data } = res.data
  //       this.dishList = data
  //     } else {
  //       this.$message.error(res.data.desc)
  //     }
  //   })
  // }

  // 删除套餐菜品
  delDishHandle(index: any) {
    this.dishTable.splice(index, 1)
    this.checkList = this.dishTable
    // this.checkList.splice(index, 1)
  }

  // 获取添加菜品数据
  private getCheckList(value: any) {
    this.checkList = [...value]
  }

  // 添加菜品
  openAddDish() {
    this.seachKey = ''
    this.dialogVisible = true
    // this.$refs.adddish.open()
  }
  // 取消添加菜品
  handleClose(done: any) {
    // this.$refs.adddish.close()
    this.dialogVisible = false
    // this.checkList = JSON.parse(JSON.stringify(this.dishTable))
    // this.dialogVisible = false
  }

  // 保存添加菜品列表
  private addTableList() {
    this.dishTable = JSON.parse(JSON.stringify(this.checkList))
    this.dishTable.forEach((n: any) => {
      n.copies = 1
    })
    this.dialogVisible = false
    // 添加处理逻辑清空选中list
    this.checkList = []
  }

  private submitForm(formName: any, st: any) {
    (this.$refs[formName] as any).validate((valid: any) => {
      if (valid) {
        if (this.dishTable.length === 0) {
          return this.$message.error('套餐下菜品不能为空')
        }
        if (!this.ruleForm.image) return this.$message.error('菜品图片不能为空')
        let prams = { ...this.ruleForm } as any;
        (prams as any).price *= 100;
        prams.setmealDishes = this.dishTable.map((obj: any) => ({ copies: obj.copies, dishId: obj.dishId, name: obj.name, price: obj.price }));
        (prams as any).status = this.ruleForm ? 1 : 0
        prams.categoryId = this.ruleForm.idType
        // delete prams.dishList
        if (this.actionType == 'add') {
          delete prams.id
          addSetmeal(prams).then(res => {
            if (res && res.data && res.data.code === 1) {
              this.$message.success('套餐添加成功！')
              if (!st) {
                this.$router.push({ 'path': '/setmeal' })
              } else {
                (this as any).$refs.ruleForm.resetFields()
                this.dishList = []
                this.dishTable = []
                this.ruleForm = {
                  'name': '',
                  'categoryId': '',
                  'price': '',
                  'code': '',
                  'image': '',
                  'description': '',
                  'dishList': [],
                  'status': true,
                  'id': '',
                  idType: ''
                } as any
                this.imageUrl = ''
              }
            } else {
              this.$message.error(res.data.msg)
            }
          }).catch(err => {
            this.$message.error('请求出错了：' + err)
          })
        } else {
          delete prams.updateTime
          editSetmeal(prams).then(res => {
            if (res.data.code === 1) {
              this.$message.success('套餐修改成功！')
              this.$router.push({ 'path': '/setmeal' })
            } else {
              // this.$message.error(res.data.desc || res.data.message)
            }
          }).catch(err => {
            this.$message.error('请求出错了：' + err)
          })
        }
      } else {
        // console.log('error submit!!')
        return false
      }
    })
  }

  private handleAvatarSuccess(response: any, file: any, fileList: any) {
    // this.imageUrl = response.data
    // this.imageUrl = `http://172.17.2.120:8080/common/download?name=${response.data}`
    this.imageUrl = `${baseUrl}/common/download?name=${response.data}`
    this.ruleForm.image = response.data
  }

  private beforeAvatarUpload(file: any) {
    // console.log(file)
  }
}
