



























































































import { Component, Vue } from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
import { getOrderDetailPage, queryOrderDetailById, editOrderDetail } from '@/api/order'

@Component({
  components: {
    HeadLable
  }
})
export default class extends Vue {

  private input = ''
  private value = []
  private dialogVisible = false
  private counts: number = 0
  private page: number = 1
  private pageSize: number = 10
  private tableData = []
  private diaForm = {}

  created() {
    this.init()
  }

  initFun() {
    this.page = 1
    this.init()
  }

  init() {
    const params = {
      page: this.page,
      pageSize: this.pageSize,
      number: this.input || undefined,
      beginTime: this.value[0] || undefined,
      endTime: this.value[1] || undefined
    }
    getOrderDetailPage({ ...params }).then(res => {
      if (res.data.code === 1) {
        this.tableData = res.data.data.records
        this.counts = Number(res.data.data.total)
      } else {
        this.$message.error(res.data.msg)
      }
    }).catch((err) => {
      this.$message.error('请求出错了：' + err)
    })
  }

  getOrderType(row: any) {
    if (row.status === 1) {
      return '待付款'
    } else if (row.status === 2) {
      return '待派送'
    } else if (row.status === 3) {
      return '已派送'
    } else {
      return '已完成'
    }
  }

  // 查看详情
  goDetail(row: any) {
    this.diaForm = {}
    this.dialogVisible = true
    this.diaForm = { ...row }
  }

  // 取消，派送，完成
  cancelOrDeliveryOrComplete(status: number, id: string) {
    this.$confirm('确认更改该套餐状态?', '提示', {
      'confirmButtonText': '确定',
      'cancelButtonText': '取消',
      'type': 'warning'
    }).then(() => {
      editOrderDetail(params).then(res => {
        if (res.data.code === 1) {
          this.$message.success(status === 3 ? '订单已派送' : '订单已完成')
          this.init()
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(err => {
        this.$message.error('请求出错了：' + err)
      })
    })
    const params = {
      status,
      id
    }

  }

  handleClose() {
    this.dialogVisible = false
  }

  private handleSizeChange(val: any) {
    this.pageSize = val
    this.init()
  }

  private handleCurrentChange(val: any) {
    this.page = val
    this.init()
  }
}

