



























































































































import { Component, Vue } from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
import {getDishPage, editDish, deleteDish, dishStatusByStatus} from '@/api/dish'
import { baseUrl } from '@/config.json'

@Component({
  'name': 'foodType',
  'components': {
    HeadLable
  }
})
export default class extends Vue {
    private input: any = ''
    private counts: number = 0
    private page: number = 1
    private pageSize: number = 10
    private checkList:string[] = []
    private tableData:[] = []
    private dishState = ''

    created () {
      this.init()
    }

    getImage (image: string) {
      // return `http://172.17.2.120:8080/common/download?name=${image}`
      return `${baseUrl}/common/download?name=${image}`
    }

    initFun () {
      this.page = 1
      this.init()
    }

    private async init () {
      await getDishPage({'page': this.page, 'pageSize': this.pageSize, 'name': this.input || undefined}).then(res => {
        if (res.data.code === 1) {
          this.tableData = res.data && res.data.data && res.data.data.records
          this.counts = Number(res.data.data.total)
        }
      }).catch(err => {
        this.$message.error('请求出错了：' + err)
      })
    }

    // 添加
    private addFoodtype (st:string) {
      if (st === 'add') {
        this.$router.push({ 'path': '/fond/add' })
      } else {
        this.$router.push({ 'path': '/fond/add', 'query': {'id': st}})
      }
    }

    // 删除
    private deleteHandle (type: string, id:any) {
      if (type === '批量' && id === null) {
        if (this.checkList.length === 0) {
          return this.$message.error('请选择删除对象')
        }
      }
      this.$confirm('确认删除该菜品, 是否继续?', '提示', {
        'confirmButtonText': '确定',
        'cancelButtonText': '取消',
        'type': 'warning'
      }).then(() => {
        deleteDish(type === '批量' ? this.checkList.join(',') : id).then(res => {
          if (res && res.data &&res.data.code === 1) {
            this.$message.success('删除成功！')
            this.init()
          } else {
            this.$message.error(res.data.msg)
          }
        }).catch(err => {
          this.$message.error('请求出错了：' + err)
        })
      })
    }

    //状态更改
    private statusHandle (row:any) {
      let params:any = {}
      if (typeof row === 'string' ) {
        if (this.checkList.length === 0) {
          this.$message.error('批量操作，请先勾选操作菜品！')
          return false
        }
        params.id = this.checkList.join(',')
        params.status = row
      } else {
        params.id = row.id
        params.status = row.status ? '0' : '1'
      }
      this.dishState = params
      this.$confirm('确认更改该菜品状态?', '提示', {
        'confirmButtonText': '确定',
        'cancelButtonText': '取消',
        'type': 'warning'
      }).then(() => {
        // 起售停售---批量起售停售接口
        dishStatusByStatus(this.dishState).then(res => {
          if (res && res.data && res.data.code === 1) {
            this.$message.success('菜品状态已经更改成功！')
            this.init()
          } else {
            this.$message.error(res.data.msg)
          }
        }).catch(err => {
            this.$message.error('请求出错了：' + err)
        })
      })
    }

    // 全部操作
    private handleSelectionChange (val:any){
      let checkArr:any[] = []
      val.forEach((n: any) => {
        checkArr.push(n.id)
      })
      this.checkList = checkArr
    }

    private handleSizeChange (val: any) {
      this.pageSize = val
      this.init()
    }

    private handleCurrentChange (val: any) {
      this.page = val
      this.init()
    }

}
