





































































import { Component, Vue } from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
import { getSetmealPage, editSetmeal, deleteSetmeal, setmealStatusByStatus } from '@/api/setMeal'
import { baseUrl } from '@/config.json'

@Component({
  'name': 'package',
  'components': {
    HeadLable
  }
})
export default class extends Vue {
  private input: any = ''
  private counts: number = 0
  private page: number = 1
  private pageSize: number = 10
  private checkList: any[] = []
  private tableData: [] = []

  created() {
    this.init()
  }

  getImage(image: string) {
    // return `http://172.17.2.120:8080/common/download?name=${image}`
    return `${baseUrl}/common/download?name=${image}`
  }

  private async init() {
    await getSetmealPage({ 'page': this.page, 'pageSize': this.pageSize, 'name': this.input || undefined }).then(res => {
      if (res && res.data && res.data.code === 1) {
        this.tableData = res.data.data.records
        this.counts = Number(res.data.data.total)
      } else {
        this.$message.error(res.data.msg)
      }
    }).catch(err => {
      this.$message.error('请求出错了：' + err)
    })
  }

  // 添加更改
  private addSetMeal(st: any) {
    // console.log(3333, st)
    if (st === 'add') {
      this.$router.push({ 'path': '/setmeal/add' })
    } else {
      this.$router.push({ 'path': '/setmeal/add', 'query': { 'id': st.id } })
    }
  }

  // 删除
  private deleteHandle(type: string, id: any) {
    if (type === '批量' && id === null) {
      if (this.checkList.length === 0) {
        return this.$message.error('请选择删除对象')
      }
    }
    this.$confirm('确定删除该套餐?', '提示', {
      'confirmButtonText': '确定',
      'cancelButtonText': '取消',
      'type': 'warning'
    }).then(() => {
      deleteSetmeal(type === '批量' ? this.checkList.join(',') : id).then(res => {
        if (res.data.code === 1) {
          this.$message.success('删除成功！')
          this.init()
        } else {
          this.$message.error(res.data.msg)
        }

      }).catch(err => {
        this.$message.error('请求出错了：' + err)
      })
    })
  }

  //状态更改
  private statusHandle(row: any) {
    let params: any = {}
    if (typeof row === 'string') {
      if (this.checkList.length == 0) {
        this.$message.error('批量操作，请先勾选操作菜品！')
        return false
      }
      params.ids = this.checkList.join(',')
      params.status = row
    } else {
      params.ids = row.id
      params.status = row.status ? '0' : '1'
    }

    this.$confirm('确认更改该套餐状态?', '提示', {
      'confirmButtonText': '确定',
      'cancelButtonText': '取消',
      'type': 'warning'
    }).then(() => {
      setmealStatusByStatus(params).then(res => {
        if (res.data.code === 1) {
          this.$message.success('套餐状态已经更改成功！')
          this.init()
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(err => {
        this.$message.error('请求出错了：' + err)
      })
    })
  }

  // 全部操作
  private handleSelectionChange(val: any) {
    let checkArr: string[] = []
    val.forEach((n: any) => {
      checkArr.push(n.id)
    })
    this.checkList = checkArr
  }

  private handleSizeChange(val: any) {
    this.pageSize = val
    this.init()
  }

  private handleCurrentChange(val: any) {
    this.page = val
    this.init()
  }

}
