










































































































import { Component, Vue } from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
import SelectInput from './components/SelectInput.vue'
// getFlavorList口味列表暂时不做 getDishTypeList
import { queryDishById, addDish, editDish, getCategoryList, commonDownload } from '@/api/dish'
import { baseUrl } from '@/config.json'
@Component({
  'name': 'addShop',
  'components': {
    HeadLable,
    SelectInput
  }
})
export default class extends Vue {
  private restKey: number = 0
  private textarea: string = ''
  private value: string = ''
  private imageUrl: string = ''
  private actionType: string = ''
  private dishList: string[] = []
  private dishFlavorsData: any[] = []
  private dishFlavors: any[] = []
  private vueRest = '1'
  private index = 0
  private inputStyle = { 'flex': 1 }
  private ruleForm = {
    'name': '',
    'id': '',
    'price': '',
    'code': '',
    'image': '',
    'description': '',
    'dishFlavors': [],
    'status': true,
    categoryId: ''
  }

  get rules() {
    return {
      'name': [
        { 'required': true, 'message': '请填写菜品名称', 'trigger': 'blur' }
      ],
      'categoryId': [
        { 'required': true, 'message': '请选择菜品分类', 'trigger': 'change' }
      ],
      'price': [
        {
          'required': true,
          // 'message': '请填写菜品价格',
          validator: (rules: any, value: string, callback: Function) => {
            if (!value) {
              callback(new Error('请填写菜品价格'))
            } else {
              // const reg = /^\d+(\.\d{0,2})?$/
              const reg = /^((0{1}\.\d{1,2})|([1-9]\d*\.{1}\d{1,2})|([1-9]+\d*)|0)$/
              if (reg.test(String(value))) {
                // callback()
                if (String(value).includes('.')) {
                  let temp = Number(String(value).split('.')[0])
                  if (temp > 9999 || temp < 0) {
                    callback(new Error('菜品价格区间在0-9999之间'))
                  } else {
                    callback()
                  }
                } else {
                  if (Number(value) > 9999 || Number(value) < 0) {
                    callback(new Error('菜品价格区间在0-9999之间'))
                  } else {
                    callback()
                  }
                }
              } else {
                callback(new Error('菜品价格格式只能为正整数'))
              }
            }
          },
          'trigger': 'blur'
        }
      ],
      'code': [
        { 'required': true, 'message': '请填写商品码', 'trigger': 'blur' }
      ]
    }
  }

  created() {
    this.getDishList()
    // 口味临时数据
    this.getFlavorListHand()
    this.actionType = this.$route.query.id ? 'edit' : 'add'
    if (this.$route.query.id) {
      this.init()
    }
  }

  private selectHandle(val: any, key: any, ind: any) {
    const arrDate = [...this.dishFlavors]
    arrDate[key] = JSON.parse(JSON.stringify(this.dishFlavorsData[ind]))
    this.dishFlavors = arrDate
  }

  private async init() {
    queryDishById(this.$route.query.id).then(res => {
      if (res && res.data && res.data.code === 1) {
        this.ruleForm = { ...res.data.data }
        this.ruleForm.price = String(res.data.data.price / 100)
        this.ruleForm.status = res.data.data.status == '1'
        this.dishFlavors = res.data.data.flavors && res.data.data.flavors.map(obj => ({ ...obj, value: JSON.parse(obj.value) }))
        this.imageUrl = `${baseUrl}/common/download?name=${res.data.data.image}`
      } else {
        this.$message.error(res.data.msg)
      }
    })
  }

  // 按钮 - 添加口味
  private addFlavore() {
    this.dishFlavors.push({ 'name': '', 'value': [] }) // JSON.parse(JSON.stringify(this.dishFlavorsData))
  }

  // 按钮 - 删除口味
  private delFlavor(ind: number) {
    this.dishFlavors.splice(ind, 1)
  }

  // 按钮 - 删除口味标签
  private delFlavorLabel(index: number, ind: number) {
    this.dishFlavors[index].value.splice(ind, 1)
  }

  //口味位置记录
  private flavorPosition(index: number) {
    this.index = index
  }

  // 添加口味标签
  private keyDownHandle(val: any) {
    if (event) {
      event.cancelBubble = true
      event.preventDefault()
      event.stopPropagation()
    }

    if (val.target.innerText.trim() != '') {
      this.dishFlavors[this.index].flavorData.push(val.target.innerText)
      val.target.innerText = ''
    }
  }

  // 获取菜品分类
  private getDishList() {
    getCategoryList({ 'type': 1 }).then(res => {
      if (res.data.code === 1) {
        this.dishList = res && res.data && res.data.data
      } else {
        this.$message.error(res.data.msg)
      }
      // if (res.data.code == 200) {
      //   const {data} = res.data
      //   this.dishList = data
      // } else {
      //   this.$message.error(res.data.desc)
      // }
    })
  }

  // 获取口味列表
  private getFlavorListHand() {
    // flavor flavorData
    this.dishFlavorsData = [
      { 'name': '甜味', 'value': ['无糖', '少糖', '半糖', '多糖', '全糖'] },
      { 'name': '温度', 'value': ['热饮', '常温', '去冰', '少冰', '多冰'] },
      { 'name': '忌口', 'value': ['不要葱', '不要蒜', '不要香菜', '不要辣'] },
      { 'name': '辣度', 'value': ['不辣', '微辣', '中辣', '重辣'] }
    ]
  }

  private submitForm(formName: any, st: any) {
    (this.$refs[formName] as any).validate((valid: any) => {
      if (valid) {
        if (!this.ruleForm.image) return this.$message.error('菜品图片不能为空')
        let params: any = { ...this.ruleForm }
        // params.flavors = this.dishFlavors
        params.status = this.ruleForm ? 1 : 0
        params.price *= 100
        params.categoryId = this.ruleForm.categoryId
        params.flavors = this.dishFlavors.map(obj => ({ ...obj, value: JSON.stringify(obj.value) }))
        delete params.dishFlavors
        if (this.actionType == 'add') {
          delete params.id
          addDish(params).then(res => {
            if (res.data.code === 1) {
              this.$message.success('菜品添加成功！')
              if (!st) {
                this.$router.push({ 'path': '/fond' })
              } else {
                this.dishFlavors = []
                // this.dishFlavorsData = []
                this.imageUrl = ''
                this.ruleForm = {
                  'name': '',
                  'id': '',
                  'price': '',
                  'code': '',
                  'image': '',
                  'description': '',
                  'dishFlavors': [],
                  'status': true,
                  categoryId: ''
                }
                this.restKey++
              }
            } else {
              this.$message.error(res.data.desc || res.data.msg)
            }
          }).catch(err => {
            this.$message.error('请求出错了：' + err)
          })
        } else {
          delete params.createTime
          delete params.updateTime
          editDish(params).then(res => {
            if (res && res.data && res.data.code === 1) {
              this.$router.push({ 'path': '/fond' })
              this.$message.success('菜品修改成功！')
            } else {
              this.$message.error(res.data.desc || res.data.msg)
            }
            // if (res.data.code == 200) {
            //   this.$router.push({'path': '/fond'})
            //   this.$message.success('菜品修改成功！')
            // } else {
            //   this.$message.error(res.data.desc || res.data.message)
            // }
          }).catch(err => {
            this.$message.error('请求出错了：' + err)
          })
        }
      } else {
        return false
      }
    })
  }

  private handleAvatarSuccess(response: any, file: any, fileList: any) {
    // 拼接down接口预览
    // this.imageUrl = `http://172.17.2.120:8080/common/download?name=${response.data}`
    this.imageUrl = `${baseUrl}/common/download?name=${response.data}`
    this.ruleForm.image = response.data
  }

  private beforeAvatarUpload(file: any) {
    // console.log(file)
  }
}
