











































































































































import {Component, Vue} from 'vue-property-decorator'
import HeadLable from '@/components/HeadLable/index.vue'
// import {getClassList, addCategory, editCategory, delCategory} from '@/api/category'
import {getCategoryPage, queryCategoryById, deleCategory, editCategory, addCategory} from '@/api/category'

@Component({
  'name': 'class',
  'components': {
    HeadLable
  }
})

export default class extends Vue {
  private action: string = ''
  private counts: number = 0
  private page: number = 1
  private pageSize: number = 10
  private tableData = []
  private type = ''
  private classData: any = {
    'title': '添加菜品分类',
    'dialogVisible': false,
    'categoryId': '',
    'name': '',
    sort: ''
  }

  get rules () {
    return {
      name: [
        {
          required: true,
          trigger: 'blur',
          validator: (rule: any, value: string, callback: Function) => {
            if (!value) {
              callback(new Error( this.classData.title + '不能为空'))
            } else {
              callback()
            }
          }
        }
      ],
      sort: [
        {
          required: true,
          trigger: 'blur',
          validator: (rule: any, value: string, callback: Function) => {
            if (value || String(value) === '0') {
              const reg = /^\d+$/
              if (!reg.test(value)) {
                callback(new Error('排序只能输入数字类型'))
              } else {
                callback()
              }
            } else {
              callback(new Error('排序不能为空'))
            }
          }
        }
      ]
    }
  }

  created() {
    this.init()
  }

  // 初始化信息
  private async init() {
    await getCategoryPage({'page': this.page, 'pageSize': this.pageSize}).then(res => {
      console.log(res.data.records)
      if (String(res.data.code) === '1') {
        this.tableData = res && res.data && res.data.data && res.data.data.records
        this.counts = Number(res.data.data.total)
      } else {
        this.$message.error(res.data.desc)
      }
    }).catch(err => {
      this.$message.error('请求出错了：' + err)
    })
  }

  // 添加
  private addClass(st: any) {
    if (st == 'class') {
      this.classData.title = '新增菜品分类'
      this.type = '1'
    } else {
      this.classData.title = '新增套餐分类'
      this.type = '2'
    }
    this.action = 'add'
    this.classData.name = ''
    this.classData.sort = ''
    this.classData.dialogVisible = true
  }

  // 修改
  private editHandle(dat: any) {
    this.classData.title = '修改分类'
    this.action = 'edit'
    this.classData.name = dat.name
    this.classData.sort = dat.sort
    this.classData.id = dat.id
    this.classData.dialogVisible = true
  }

  // 关闭弹窗
  private handleClose(st: string) {
    this.classData.dialogVisible = false
  }

  //删除
  private deleteHandle(id: any) {
    this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
      'confirmButtonText': '确定',
      'cancelButtonText': '取消',
      'type': 'warning'
    }).then(() => {
      deleCategory(id).then(res => {
        if (res.data.code === 1) {
          this.$message.success('删除成功！')
          this.init()
        } else {
          // this.$message.error(res.data.msg)
        }
      }).catch(() => {
        // this.$message.error('请求出错了：' + err)
      })
    })
  }

  $refs!: {
    classData: any
  }

  //数据提交
  submitForm(st: any) {
      if (this.action === 'add') {
        this.$refs.classData.validate((value: boolean) => {
          if (value) {
            addCategory({'name': this.classData.name,'type':this.type, sort: this.classData.sort}).then(res => {
              if (res.data.code === 1) {
                this.$message.success('分类添加成功！')
                if (!st) {
                  this.classData.dialogVisible = false
                } else {
                  this.classData.name = ''
                  this.classData.sort = ''
                }
                this.init()
              } else {
                this.$message.error(res.data.desc || res.data.msg)
              }
            }).catch(err => {
              this.$message.error('请求出错了：' + err)
            })
          }
        })
    } else {
      this.$refs.classData.validate((value: boolean) => {
        if (value) {
          editCategory({'id':this.classData.id,'name': this.classData.name, sort: this.classData.sort}).then(res => {
            if (res.data.code === 1) {
              this.$message.success('分类修改成功！')
              this.classData.dialogVisible = false
              this.init()
            } else {
              this.$message.error(res.data.desc || res.data.msg)
            }
          }).catch(err => {
            this.$message.error('请求出错了：' + err)
          })
        }
      })
    }
  }

  //分页
  private handleSizeChange(val: any) {
    this.pageSize = val
    this.init()
  }

  private handleCurrentChange(val: any) {
    this.page = val
    this.init()
  }
}
